export default {
	data() {
		return {
			id:"",
			result:"",
		}
	},
	onLoad(options) {
		const that = this;
		that.id = options.id;
		that.getDetail(options.id);

	},
	methods: {
		reload(){
			this.getDetail(this.id);
		},
		getDetail(id){
			const that = this;
			let param = {
				id:id
			}
			uni.showLoading({
				title:"加载中..."
			})
			that.$api.employeeDetail(param).then(res => {
				if (res.success) {
					
					let result = res.datas;
					var nowYear = parseInt(new Date().getFullYear());
					var birdayYear = parseInt(result.birthday.substring(0,4));
					result.age = nowYear-birdayYear;
					
					if(result.gender==0){
						result.genderText = "保密";
					}
					else if(result.gender==1){
						result.genderText = "男";
					}
					else{
						result.genderText = "女";
					}
					that.result = result;
					uni.hideLoading();
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoEdit(){
			this.$Router.push({
				path:"/pages/my/manageNavs/staff/edit",
				query:{
					id:this.id
				}
			})
		}
	}
}
